import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DashboardComponent } from './dashboard.component';
import { AddUserComponent } from './components/add-user/add-user.component';
import { SearchUserComponent } from './components/search-user/search-user.component';
import { SearchSubscriberComponent } from './components/search-subscriber/search-subscriber.component';
import { ReportsComponent } from './components/reports/reports.component';
import { CashInComponent } from './components/cash-in/cash-in.component';
import { UserProfileComponent } from './components/search-subscriber/user-profile/user-profile.component';
import { TransactionsComponent } from './components/search-subscriber/transactions/transactions.component';
import { CredentailsComponent } from './components/search-subscriber/credentails/credentails.component';

const routes: Routes = [
  {
    path: '', component: DashboardComponent,
    children: [
      { path: '', redirectTo:'adduser' },
      {path: 'adduser', component:AddUserComponent},
      { path: 'searchuser', component: SearchUserComponent },
      { path: 'cashin', component: CashInComponent },
      { path: 'searchsubscriber', component: SearchSubscriberComponent },
      { path: 'reports', component: ReportsComponent },
      { path: 'profile', component: UserProfileComponent },
      { path: 'transactions', component: TransactionsComponent },
      { path: 'credentials', component: CredentailsComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }



